.product-list {
    padding-top: 50px;
    display: grid;
    width: 100%;
    align-items: center;
    grid-template-columns: repeat( auto-fit, minmax(250px, 1fr));
    grid-gap: 60px;
    background-color: #fff;

    &__button {
        cursor: pointer;
        border: none;
        font-size: 1.125rem;
        background-color: #FF0033;
        color: #fff;
        padding: 10px 17px;
        margin: 40px auto 50px;

        &--disable {
            cursor: auto;
            opacity: 0.8;
        }
    }
}