.header {
    box-sizing: border-box;
    background-color: #FFF;
    border-bottom: 2px solid black;
    width: 100%;
    padding: 10px;
    background-color: white;
    position: sticky;
    z-index: 10;
    top: 0;
    right: 0;
    
    &__container {
        margin: 0 auto;
        max-width: 1200px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }  

    &__logo-link {
        color: #1c1d2d;
        font-size: 1.75rem; // 28px
        font-weight: 900;
    }

    &__menu-list {
        list-style: none;
        display: flex;
        margin: 0;
        padding: 0;
    }

    &__list-item {
        margin-right: 30px;

        &:last-child {
            margin: 0;
        }
    }

    &__link {
        color: #1c1d2d;

        font-size: 1.25rem; // 20px
        font-weight: 500;

        transition: 0.25s;

        
        &--heart::after {
            content: url('../../assets/heart.svg');
        }

        &--cart::after {
            content: url('../../assets/cart.png');
            width: 20px;
            height: 20px;
        }

        &--active {
            color: crimson;
        }

    }
}