.product {

    display: flex;
    flex-direction: column;
    height: 470px;
    cursor: pointer;
    transition: 0.2s;
    padding: 25px 30px;
    background-color: #fff;
    color: black;
    &:hover {
        box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.39);
    }

    &__img-block {
        height: 300px;
        position: relative;
        display: flex;
        justify-content: center;
    }

    &__img {
        object-fit: contain;
        width: 100%;
        max-height: 300px;
    }

    &__rate {
        position: absolute;
        bottom: -50px;
    }

    &__title {
        text-align: center;
        font-size: 25px;
        margin-top: 50px;
    }

    &__price {
        text-align: center;
        font-size: 30px;
        margin-top: auto;
    }
}
