.product-page {
    padding: 100px 12% 100px 12%;
    background-color: white;
    height: 100%;
    &__container {
        display: flex;
        align-items: center;
    }
    &__img-block {
        display: flex;
        flex-grow: 1;
    }

    &__img {
        object-fit: contain;
        max-width: 60%;
    }

    &__content {
        max-width: 40%;
        display: flex;
        flex-grow: 2;
        flex-direction: column;
    }
    &__title {
        font-size: 30px;
    }
    &__price {
        font-size: 25px;
    }
    &__descr {
        font-size: 20px;
    }
    &__category {

        & span {
            
        }
    }
}

.btns-block {
    display: flex;
    margin-top: 40px;
}
.btn {
    position: relative;
    display: block;
    font-size: 18px;
    background-color: rgb(230, 230, 230);
    border: 2px solid black;
    padding: 6px 34px 6px 13px;
    margin-right: 30px;
    border-radius: 10px;
    white-space: nowrap;
    cursor: pointer;

    &::after {
        content: '';
        position: absolute;
        display: block;
        width: 2px;
        height: 100%;
        

        background: black;
        color: black;
        right: 23px;
        top:0;
    }

    &::before {
        content: '+';
        position: absolute;
        display: block;
        font-size: 20px;
        line-height: 100%;
        bottom: calc(50% - 10px);
        right: 6px;
    }

    &:last-child {
        margin-right: 0;
    }
}

@media (min-width: 2000px) {
    .product-page {
        &__title {
            font-size: 45px;
        }
        &__descr {
            font-size: 30px;
        }
    }
}